html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

@import "base/variables";
@import "base/mixins";
@import "reset";
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');

body {
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #000000;
  font-family: 'Open Sans', sans-serif;
  cursor: default;
}
.header {
  position: absolute;
  z-index: 20;
  top: 49px;
  width: 100%;
  right: 0;
}
.container {
  padding: 0 45px;
  max-width: 1255px;
  margin: 0 auto;
  width: 100%;
}
.hero {
  background-image: url("../images/hero.jpeg");
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  z-index: 10;
  text-align: center;
  color: #FFFFFF;
  border-radius: 0 0 30px 30px;
  padding-top: 49px;
}
.hero::after {
  position: absolute;
  border-radius: 0 0 30px 30px;
  content: "";
  top: 0;
  z-index: -1;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.hero h1 {
  font-size: 50px;
  line-height: 60px;
  max-width: 872px;
  margin: 0 auto 13px;
}
.hero p {
  font-size: 20px;
  line-height: 27px;
  max-width: 549px;
  margin: 0 auto 46px;
}
.hero a img {
  margin: 0 auto 46px;
}
.hero button {
  background-color: #F8C77C;
  border-radius: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  min-height: 62px;
  padding: 0 110px;
  margin: 0 auto;
  color: #000000;
}

.lang  {
  display: flex;
  justify-content: right;
  margin-bottom: 18px;
}
.lang .lang--active {
  background-color: #fff;
  color: #454545;
}
.lang a {
  background-color: rgba(#5E5E5E, 0.4);
  border-radius: 10px;
  font-size: 17px;
  line-height: 20px;
  color: #DADADA;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu {
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin: 0 0 0 auto;
}
.menu li:not(:last-child) {
  margin-bottom: 13px;
}
.menu li {
  font-size: 17px;
  line-height: 20px;
  color: #FFFFFF;
  text-align: right;
}
.menu li a {
  width: fit-content;
  text-align: right;
  margin-left: auto;
}